import React, { Suspense } from "react";
import { isSocialEmbed, isTagEmbed, isEmbed } from "../../../../../actions/api";
import MediaIcon from "../../../Elements/MediaIcon/MediaIcon";
import Image from "../../../Elements/Media/Image";
import Video from "../../../Elements/Media/Video";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Media = ({ itemData, wallID, ThemeID, ownerId, wall }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  return <div className="tb_px_media_wrap">
    {isSocialEmbed && wall.Personalization.interactive ? <Suspense fallback={<></>}><Like likeClass={`tb_px_like ${!wall.ThemeRule.socialAction ? 'tb_px_like_bottom':''}`} postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {isEmbed && !autoPlay ? <MediaIcon itemData={itemData}  IconClass="tb_w_c_media_icon" show={isEmbed} isCenter={false} /> : null }
    {autoPlay ?
      <Video VideoClass="tb_px_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={false} isCover={true} controls={false} autoPlay={true} muted={true} />
      :
      <Image ImageClass="tb_px_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={false} ThemeRule={wall.ThemeRule} hotspot={true} />
    }
  </div>
}
export default Media